.browseBranch-heading {
	padding-left: 0.5rem;
	color: $browseHeadingColor;
	@include noFocusStyle;
	@include fontSizeAlways(1em);

	@include mqGreaterThan(smallMedium) {
		margin-left: 0;
		margin-right: 1.5rem;
		margin-bottom: 1rem;
		padding-bottom: 1rem;
		padding-left: 0;
		border-bottom: 1px solid $browseHeadingColor;
	}

	@include mqGreaterThan(mediumLarge) {
		margin-left: 1.5rem;
	}
}

.browseBranch-list {
	@include noBullets;
}