$easeInOutQuint: cubic-bezier(0.860, 0.000, 0.070, 1.000);
$browseTransformTransition: 300ms;
$browsePanelLeftBorderWidth: 16px;
$browsePanelLeftPadding: 1rem;
$browseThreePanelOffset: -50%;

$browsePanelSmallMediumWidth: 40%;
$browsePanelMediumLargeWidth: #{(1/3) * 100%};

.browsePanel {
	@extend .column;
	background: $browseBgColor;
	position: relative;
	flex: 0 0 auto; // Using flex-basis auto to fallback to width. IE doesn't like calc on flex-basis, as used later on.

	@include setPropWithMq(greater, width,
		(
			all: calc(100% - #{$browsePanelLeftBorderWidth}),
			smallMedium: $browsePanelSmallMediumWidth,
			mediumLarge: $browsePanelMediumLargeWidth
		)
	);

	@include mqGreaterThan(smallMedium) {
		margin: 0;
		padding: 1.75rem 0 1.75rem $browsePanelLeftPadding;
	}

	&:nth-child(n+2) { // All but the first panel (semantically).
		display: none;

		@include mqGreaterThan(smallMedium) {
			display: block;
		}
	}

	&:nth-last-child(n+2) { // All but the last panel (semantically).
		@include mqGreaterThan(smallMedium) {
			border-left: $browsePanelLeftBorderWidth solid $browsePanelLeftBorderColor;

			&:first-child { // Only the semantic first panel, never the last.
				background-color: $browseLastPanelBgColor;
			}
		}

		@include mqGreaterThan(mediumLarge) {
			width: $browsePanelMediumLargeWidth;
		}
	}
}

// order is specified to make sure the columns are displayed in reverse.
// Using row-reverse in combination with justify-content on the parent causes
// the excess width to overflow invisibly to the left in IE.
.browsePanel--level-1 {
	z-index: 1;
	order: 1;
}

.browsePanel--level-2 {
	z-index: 2;
	order: 2;
	@include mqGreaterThan(smallMedium) {
		transition: transform #{$browseTransformTransition} #{$easeInOutQuint};

		.browse--panel-quantity-4 & {
			transform: translateX(-80%);
		}
	}

	@include mqGreaterThan(mediumLarge) {
		.browse--panel-quantity-4 & {
			transform: translateX(-50%);
		}
	}
}

.browsePanel--level-3 {
	z-index: 3;
	order: 3;
}

.browsePanel--level-4 {
	z-index: 4;
	order: 4;
}

.browsePanel--level-2,
.browsePanel--level-3,
.browsePanel--level-4 {
	@include mqGreaterThan(smallMedium) {
		.browse--panel-quantity-3 & {
			transform: translateX(-50%);
		}
	}
}

.browsePanel--level-3,
.browsePanel--level-4 {
	@include mqGreaterThan(smallMedium) {
		transition: transform #{$browseTransformTransition} #{$easeInOutQuint};

		.browse--panel-quantity-4 & {
			transform: translateX(-150%);
		}
	}

	@include mqGreaterThan(mediumLarge) {
		.browse--panel-quantity-4 & {
			transform: translateX(-100%);
		}
	}
}

.browsePanel--enter {
	opacity: 0.01;
	transition: opacity #{$browseTransformTransition} linear;

	&.browsePanel--level-1,
	&.browsePanel--level-2 {
		transition-delay: 0ms;
	}
}

.browsePanel--enter-active {
	opacity: 1;
	transition: opacity #{$browseTransformTransition} linear #{$browseTransformTransition / 2};

	.browsePanel--level-1,
	.browsePanel--level-2 {
		transition-delay: 0ms;
	}
}