$menuBarButtonTextHoverColor: getColor(zwart) !default;
$menuBarButtonTextActiveColor: getColor(zwart) !default;
$menuBarButtonFillHoverColor: getColor(zwart) !default;
$menuBarButtonFillActiveColor: getColor(zwart) !default;

$menuBarButtonContextBackgroundColor: getColor(hemelblauw default) !default;
$menuBarButtonContextTextActiveColor: getColor(wit) !default;
$menuBarButtonContextIconFillActiveColor: getColor(wit) !default;

$menuBarButtonToTopBackgroundColor: rgba(getColor(hemelblauw lighter), 0.5) !default;
$menuBarButtonToTopTextColor: getColor(zwart) !default;
$menuBarButtonToTopFillColor: getColor(zwart) !default;

$menuBarButtonIsOpenedBackgroundHoverColor: getColor(wit) !default;
$menuBarButtonIsOpenedTextHoverColor: getColor(zwart) !default;
$menuBarButtonIsOpenedFillHoverColor: getColor(zwart) !default;

.menuBar-button {
	width: $menuBarWidthSmall;
	height: $menuBarWidthSmall;
	margin-top: 1rem;
	margin-bottom: 1rem;
	padding: 0.15rem;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	left: 0;
	background: none;
	overflow: hidden;
	border: none;
	color: $menuBarButtonColor;
	transition: background-color, color 200ms ease-in-out;
	text-align: center;

	@include mqGreaterThan(small) {
		width: $menuBarWidthLarge;
		height: $menuBarWidthLarge;
		padding-top: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);
		padding-bottom: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);
		margin-top: (10rem/17);
		margin-bottom: (10rem/17);
	}

	@include mqGreaterThan(large) {
		width: $menuBarWidthLarge;
		height: $menuBarWidthLarge;
		margin-top: (12rem/18);
		margin-bottom: (12rem/18);
	}

	@include mqGreaterThan(extraExtraLarge) {
		width: $menuBarWidthExtraLarge;
		padding: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);
		display: block;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		width: $menuBarWidthExtraExtraLarge;
		margin-top: 0.9rem;
		margin-bottom: 0.9rem;
	}

	@include hoverEtc {
		background-color: $menuBarButtonHoverColor;
		color: inherit;

		.menuBar-icon {
			fill: $menuBarButtonFillHoverColor;
		}
	}

	@media (pointer: coarse) {
		&:hover {
			background-color: transparent;
			color: $menuBarButtonColor;

			.menuBar-icon {
				fill: $menuBarButtonColor;
			}
		}
	}

	&.is-opened {
		background-color: $menuBarButtonIsOpenedColor;
		color: $menuBarButtonTextActiveColor;

		.menuBar-icon {
			fill: $menuBarButtonFillActiveColor;
			transform: scale(0.8);
		}

		&:hover {
			background-color: $menuBarButtonIsOpenedBackgroundHoverColor;
			color: $menuBarButtonIsOpenedTextHoverColor;

			.menuBar-icon {
				fill: $menuBarButtonIsOpenedFillHoverColor;
			}
		}
	}

	&:last-child {
		margin-bottom: 0;
	}
	// less top-/bottom-margin on landscape and zoomed-in screens
	@include mqLesserThan(smallHeight) {
		margin: {
			top: 0.14rem;
			bottom: 0.14rem;
		}
	}
}

.menuBar-button--context {

	&.is-opened {
		background-color: $menuBarButtonContextBackgroundColor;
		color: $menuBarButtonContextTextActiveColor;

		.menuBar-icon {
			fill: $menuBarButtonContextIconFillActiveColor;
		}
	}
}

.menuBar-button--toTop {
	background-color: $menuBarButtonToTopBackgroundColor;
	color: $menuBarButtonToTopTextColor;
	margin-bottom: 0;

	.menuBar-icon {
		fill: $menuBarButtonToTopFillColor;
	}
}

.menuBar-button-inner {
	margin: 0 auto; // needed for mobile Safari when .menuBar-button is a <button> element

	@include mqGreaterThan(extraExtraLarge) {
		display: flex;
		flex-direction: row;
		align-items: center;
	}
}

.menuBar-button-text {
	position: relative;
	text-transform: uppercase;
	@include fontSize(menuLabelsExtraSmall);
	display: block;

	@include mqGreaterThan(small) {
		@include fontSize(menuLabelsSmall);
	}

	@include mqGreaterThan(extraLarge) {
		@include fontSize(menuLabelsMedium);
	}

	@include mqGreaterThan(extraExtraLarge) {
		@include fontSize(menuLabels);
		padding-right: $pad-half;
		text-transform: inherit;
		display: inline;

		text-align: right;
		order: 1;
		flex: 1 1 auto;
	}
}