$gutter: 1rem !default;
$mobileGutter: 1rem !default;
$baseColor: getColor(hemelblauw default) !default;
$buttonColor: getColor(hemelblauw default) !default;
$filterIconWidth: 2rem !default;

$headerBgColor: getColor(hemelblauw light2) !default;
$headerTitleColor: getColor(hemelblauw darkest) !default;
$searchButtonHoverColor: getColor(oranje text) !default;
$itemTargetHoverColor: getColor(oranje text) !default;
$itemIntroColor: getColor(grijs darkest) !default;
$searchResultsMoreButtonIconFill: getColor(wit) !default;

.search-header {
	padding-top: 1.5rem;
	flex: 0 0 auto;
	background-color: $headerBgColor;
	border-top: 4px solid $brandBarTopBg;
	margin-top: -4px;
	position: sticky;
	top: 0;
	z-index: 5;
}

.search-header-title {
	margin-top: 0;
	color: $headerTitleColor;
	font-size: 1rem;
	font-weight: bold;
}

.search-gutter {
	margin: {
		left: $gutter;
		right: $gutter;
	}
}

.search-interface {
	&#{&} {
		position: relative;
		margin-bottom: 1rem;
		align-items: stretch;
		max-width: 40rem;
	}
}

.search-input {
	padding-left: 1rem;
	border: 1px solid getColor(hemelblauw darkest);

	&::-webkit-search-cancel-button,
	&::-webkit-search-decoration {
		-webkit-appearance: searchfield-cancel-button;
	}
	&::-ms-clear {
		display: block;
	}
}

.search-clear-input {
	position: absolute;
	width: 1.5rem;
	height: 1.5rem;
	top: 11px;
	right: 18%;
	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: none;
	}
}

.search-button {
	@include moreButton($buttonColor);
	width: auto;
	padding-left: 0.75rem;
	padding-right: 0.75rem;
	
	@include mqGreaterThan(smallMedium) {
		padding-left: 2rem;
		padding-right: 2rem;
	}

	@include hoverEtc {
		background-color: $searchButtonHoverColor;
		border-top-color: darken($searchButtonHoverColor, 7%);
	}
}

.search-button-text {
	display: none;

	@include mqGreaterThan(smallMedium) {
		display: inline;
	}
}

.search-button-icon {
	width: 1.5rem;
	height: 1.5rem;
	fill: getColor(wit);

	@include mqGreaterThan(smallMedium) {
		display: none;
	}
}

.search-suggestions {
	background-color: getColor(wit);
	border: 1px solid getColor(hemelblauw darkest);
	border-top: none;
	padding-left: 1rem;
}

.search-suggestions-list {
	@include noBullets;
}


.search-suggestion-link {
	text-decoration: none;
	border: none;
	display: flex;
}

.search-suggestion-icon {
	transform: rotate(45deg);
}


.search-tabs {

	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: none;
	}


	&.tabs {
		margin-left: $gutter;
	}
}

.search-tabs-item {
	@include cleanButton;
	width: 100%;
	@include lineHeight(searchTabItem);
}

.search-tab {
	padding: 10px;
	background-color: $baseColor;
	border-bottom: 3px solid darken($baseColor, 7%);
	color: getColor(wit);
	transition: all 0.3s ease-in-out;

	&:hover {
		background-color: darken($baseColor, 7%);
	}

	.tab-link {
		//overwrite a-rules
		transition: none;
		//overwrite tab-link styles from ow-pagina

		&:before {
			position: relative;
			background: none;
		}
		&:hover {
			color: getColor(wit);
			fill: getColor(wit);
		}
	}
}

.search-tab-active {

	&:hover {
		background-color: getColor(wit);
	}

	.tab-link {
		font-weight: bold;
		color: getColor(hemelblauw darkest);
		fill: getColor(hemelblauw darkest);
		background-color: getColor(wit);
		border-bottom: none;

		@include hoverEtc {
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
		}

		&:before {
			position: relative;
			background: none;
		}
	}
}

.search-filters {

	@include mqLesserThan($twoColumnGridBreakpoint) {
		background: white;
		border-bottom: 1px solid getColor(grijs border);
	}
}

.search-filters-mobile {
	display: none;
	position: relative;
	margin-left: 1rem;
	margin-right: 1rem;

	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: flex;
		.filter-icon {
			width: $filterIconWidth;
			height: 1.5rem;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			margin-top: auto;
			margin-bottom: auto;
		}

		.select-icon {
			width: 2rem;
			height: 2rem;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			margin-top: auto;
			margin-bottom: auto;
			fill: getColor(hemelblauw darkest);
		}
	}
}

.search-filters-mobile-select {
	width: 100%;
	font-weight: bold;
	color: getColor(hemelblauw default);
	padding: {
		top: 0.8rem;
		bottom: 0.8rem;
		left: $filterIconWidth;
	}
	appearance: none;
	background: none;
	border: none;
	position: relative;
}

.search-filters-mobile-option {
	color: getColor(hemelblauw darkest);
	&:checked {
		font-weight: bold;
	}
}

.search-results {
	background-color: getColor(wit);
	padding-bottom: $gutter;
	position: relative;
	top: 0;
	flex: 1 1 0;
	-webkit-overflow-scrolling: touch;
}

.search-results-list {
	transition-property: opacity;
	transition-duration: 200ms;
	list-style: none;
	padding-top: $space / 2;
	padding-left: 0;

	li:before {
		content: '';
	}
}

.search-results-message {
	padding-top: $space;
}

.search-results-item {
	display: flex;
}

.search-results-item-link {
	display: block;
	border-bottom: 0;

	@include hoverEtc {
		.search-results-item-title {
			text-decoration: underline;
		}
		.search-results-item-target {
			color: $itemTargetHoverColor;
		}
		.search-results-item-intro {
			color: $itemIntroColor;
		}
	}
}

.search-results-item-icon {
	display: none;
}

.search-results-item-title {
	margin-top: 0.5rem;
	color: $baseColor;
}

.search-results-item-target {
	display: block;
	color: $baseColor;
	word-break: break-all;
	word-break: break-word;
}

.search-results-more-button {
	margin-left: 1rem;
	margin-bottom: 2rem;
	background-color: $buttonColor;
	border: none;
	border-top: 3px solid darken($buttonColor, 7%);
	&:hover {
		background-color: darken($buttonColor, 7%);
	}
	@include mqLesserThan(medium) {
		margin-left: 1rem;
	}
}

.search-results-more-button-icon {
	@include rotateYoYo;
	fill: $searchResultsMoreButtonIconFill;
	margin-left: 0.5em;
}

.search-results-amount {
	margin-top: 1rem;
	min-height: 3rem;
}

.search-results-query {
	font-weight: bold;

	&:before {
		content: '';
		font-weight: normal;
	}

	&:after {
		content: '';
		font-weight: normal;
	}
}

.search-facetSelectorIcon {
	width: 1rem;
	height: 1rem;
	display: none;

	.search-tab-active & {
		@include mqLesserThan(medium) {
			display: block;
		}
	}
}

.search-errorIcon {
	width: 1.5rem;
	height: 1.5rem;
	fill: getColor(rood default);
}

.search-errorText {
	margin-left: 0.2em;
	margin-top: 0.1em;
	// Enough contrast when text is bold
	color: getColor(rood default);
	font-weight: bold;
}