/*------------------------------------*\
	BUTTONS
\*------------------------------------*/

// remove default styling from button
@mixin cleanButton {
	background: none;
	border: none;
	padding: 0;
	color: inherit;
	font-weight: inherit;
	line-height: inherit;
	text-align: left;
	text-transform: none;
}

@mixin moreButton($buttonColor) {
	width: 20%;
	padding: 10px 20px;
	background-color: $buttonColor;
	border: none;
	border-top: 3px solid darken($buttonColor, 7%);
	color: getColor(wit);
	text-align: center;
	@include hoverEtc {
		background-color: darken($buttonColor, 7%);
		color: getColor(wit);
	}

	@include mqLesserThan(mediumLarge) {
		width: auto;
	}
}

// Main class
.btn {
	padding: 0.75rem;
	display: inline-block;
	background-color: getColor(hemelblauw default);
	border: 0;
	@include lineHeight(button);
	text-align: center;
	transition: background-color 0.3s ease-in-out;

	@include theme((background-color: foreground), '.btn', true);

	&:hover, &:focus {
		background-color: getColor(hemelblauw darkest);
		color: getColor(wit);
		fill: getColor(wit);

		@include theme((background-color: foregroundDarker), '.btn', true);
	}

	&.disabled {
		background: getColor(grijs light);
		color: getColor(grijs darkest);
	}

	&--uc {
		text-transform: uppercase;
	}

	&--marginRight {
		margin-right: $pad-half;
	}

	&--green {
		background-color: getColor(groen default);

		&:hover, &:focus {
			background: darken(getColor(groen default), 5%);
		}
	}
}

.btn,
article .btn.btn.btn { /* prevent override by `article a:not(.interfaceLink)` of default button color  */
	color: getColor(wit);
	fill: getColor(wit);
}

.btn-large {
	padding: $pad-half;
	text-transform: uppercase;
	background: getColor(hemelblauw default);
	@include fontSize(h2 small);
	font-weight: normal;
}

.btn-asLink {
	background-color: transparent;
	border: none;
	padding: 0;
	color: getColor(hemelblauw default);
	text-transform: none;

	> span {
		border-bottom: 1px solid transparent;
		transition: 0.3s all ease-in-out;
	}

	@include hoverEtc {
		background-color: transparent;
		color: getColor(hemelblauw default);

		> span {
			border-bottom-color: getColor(hemelblauw default);
		}
	}

	svg {
		display: inline-block;
		fill: getColor(hemelblauw default);
		position: relative;
		top: 3px;
	}
}

.text-btn {
	font-style: italic;
}

.cleanButton {
	@include cleanButton;
}

.submitButton {
	$buttonColor: getColor(violet default);
	$buttonDarkColor: darken($buttonColor, 7%);

	padding: 0.35rem 1.25rem;
	position: relative;
	background-color: $buttonColor;
	border: none;

	&:before {
		width: 100%;
		height: 3px;
		content: '';
		background-color: $buttonDarkColor;
		position: absolute;
		top: 0;
		left: 0;
		transition: height 0.15s ease-in-out;
		z-index: 1;
	}

	&:active {
		top: 1px;
	}

	&:hover {
		color: getColor(wit);

		&:before {
			height: 100%;
		}
	}

	&--error {
		$errorButtonColor: getColor(rood default);
		$errorButtonDarkColor: darken($errorButtonColor, 7%);

		background-color: $errorButtonColor;
		border: none;
		border-top: 3px solid $errorButtonDarkColor;

		@include hoverEtc {
			background-color: $errorButtonDarkColor;
		}
	}

	&-wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;

		&.align-right {
			justify-content: flex-end;

			@include mqGreaterThan(large) {
				width: 82.8%;
			}
		}
	}

	&-text {
		position: relative;
		z-index: 2;
	}
}