$relatedBarBg: getColor(oranje lightest) !default;
$relatedBarListItemLinkColor: getColor(hemelblauw default) !default;
$relatedBarListItemLinkHoverColor: getColor(zwart) !default;

.relatedBar {
	$marginMap: multiplyProps(getFontSize(h2), 1.5);
	@include setPropWithMq(greater, margin-bottom, $marginMap);
	background-color: $relatedBarBg;
	visibility: hidden;
	height: 0;

	@include mqGreaterThan(smallMedium) {
		height: auto;
		padding: 0.4rem 0;
		visibility: visible;
	}
}

.relatedBar--atBottom {
	height: auto;
	margin-top: 2rem;
	padding: 0.4rem 0;
	visibility: visible;
}

.relatedBarAlignWrapper {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: baseline;
}

.relatedBarInner {
	padding: 0 1rem;
}

.relatedBarLabel {
	margin-top: 0.5rem;
	margin-right: 0.5rem;
	font-weight: bold;

	&.h4 {
		font-size: 0.875rem;
	}
}

.relatedBarList {
	@include noBullets;
	display: flex;
	flex-wrap: wrap;
}

.relatedBarListItem {
	margin-right: 0.5rem;
	padding-top: 0.1rem;
	padding-bottom: 0.1rem;
	font-size: 0.875rem;

	&:after {
		content: '\002c'; // comma
	}

	&:last-child:after {
		content: '';
	}
}

.relatedBarListItemLink {
	color: $relatedBarListItemLinkColor;

	@include hoverEtc {
		color: $relatedBarListItemLinkHoverColor !important; /* stylelint-disable-line declaration-no-important */ // To overrule article a:not(.interfaceLink):hover
	}
}