.menuBarContainer {
	position: fixed;
	left: 0;
	top: 0;
	bottom: 0;
	@include zIndex(menuBar);

	@include mqGreaterThan(largest) {
		left: 50%;
		margin-left: negative(map-get($maxPageWidth, rem) / 2);
	}
}

.is-menuBarContainer-opened {
	transform: translateX(100vw) translateX(negative($menuBarWidthSmall + 1rem));

	@include mqGreaterThan(small) {
		transform: translateX(100vw) translateX(negative($menuBarWidthLarge + 1rem));
	}
	@include mqGreaterThan(large) {
		transform: translateX(100vw) translateX(negative($menuBarWidthExtraLarge + 1rem));
	}

	@include mqGreaterThan(extraExtraLarge) {
		transform: translateX(100vw) translateX(negative($menuBarWidthExtraExtraLarge + 1rem));
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		transform: translateX(52rem);
	}
}