$browseListItemColor: getColor(hemelblauw darker) !default;
$browseListItemHoverColor: getColor(hemelblauw darker) !default;
$browseListItemIconColor: getColor(hemelblauw darker) !default;
$browseListItemDimmedColor: getColor(hemelblauw darker) !default;

.browseListItem {
	padding: 0;
}

.browseListItem-link {
	@include cleanButton;
	width: 100%;
	padding: 0.5rem;
	color: $browseListItemColor;
	display: inline-flex;
	align-items: center;

	.browse--panel-quantity-4 .browsePanel--level-2 &,
	.browseStem & {
		color: $browseListItemDimmedColor;

		&:hover {
			color: $browseListItemHoverColor;
		}
	}

	.browseStem & {
		font-size: 1em;
	}

	@include hoverEtc {
		background-color: transparent;
		color: $browseListItemHoverColor;

		.browseListItem-linkIcon {
			position: relative;
			left: 4px;
		}
	}

	@include mqGreaterThan(smallMedium) {
		font-size: 0.9em;
	}

	@include mqGreaterThan(mediumLarge) {
		padding: 0.65rem 1.2rem 0.65rem 1.5rem;
	}
}

.browseListItem-linkText {
	flex: 1;
}

.browseListItem-link--active {
	background-color: white;
	font-weight: bold;

	@include hoverEtc {
		background-color: white;
	}
}

.browseListItem-linkIcon {
	width: 1.5rem;
	height: 1.5rem;
	margin-left: auto;
	position: relative;
	left: 0;
	fill: $browseListItemIconColor;
	transition: left 0.2s ease-in-out;

	&.zandloper {
		@include rotateYoYo;
	}
}