.browseLeaf-heading {
	color: $browseHeadingColor;
	@include noFocusStyle;
	@include fontSizeAlways(1em);

	@include mqGreaterThan(smallMedium) {
		margin-left: 1.5rem;
		margin-right: 1.5rem;
		margin-bottom: 1rem;
		padding-bottom: 1rem;
		border-bottom: 1px solid $browseHeadingColor;
	}
}

.browseLeaf-list {
	padding-right: 1.5rem;

	@include mqGreaterThan(smallMedium) {
		padding-left: 1.5rem;
		padding-right: 1.5rem;
	}

	.linkList-item {
		@include mqGreaterThan(smallMedium) {
			font-size: 0.9em;
		}
	}
}

.browseLeafOverview-subheading {
	margin-top: 1.85em;
	color: $browseSubHeadingColor;
	@include fontSizeAlways(1em);
}