.browseStem {
	@include mqGreaterThan(smallMedium) {
		padding-top: 1rem;
		padding-left: 0.5rem;
	}

	@include mqGreaterThan(extraLarge) {
		padding-left: 2.75rem;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		padding-left: 3.75rem;
	}
}

.browseStem-heading {
	padding-left: 0.5rem;
	color: $browseHeadingColor;
	@include noFocusStyle;
	@include fontSizeAlways(1em);

	@include mqGreaterThan(smallMedium) {
		margin-left: 1.5rem;
		margin-right: 1.5rem;
		margin-bottom: 1rem;
		padding-bottom: 1rem;
		border-bottom: 1px solid $browseHeadingColor;
		@include accAlwaysHide;
	}
}

.browseStem-list {
	@include noBullets;
}